@import "https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600&display=swap";
* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-family: Poppins, sans-serif;
  font-size: 16px;
}

html, body {
  min-width: 320px;
  height: 100%;
}

body {
  color: #344f10;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1;
}

input, button, textarea {
  font-family: Poppins, sans-serif;
  font-size: inherit;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
}

a {
  color: inherit;
}

a:link, a:visited, a:hover {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

[class*="__container"] {
  box-sizing: content-box;
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 479.98px) {
  [class*="__container"] {
    padding: 0 27px;
  }
}

.input {
  color: #333;
  background-color: #0000;
  border-bottom: 1px solid #bdbdbd;
  outline: none;
  width: 100%;
  padding: 5px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  transition: color .3s, border-bottom .3s;
}

@media (width >= 991.98px) {
  .input {
    padding: 10px 0;
    font-size: 18px;
    line-height: 27px;
  }
}

.input::placeholder {
  color: #828282;
}

.input:focus {
  border-bottom: 1px solid #333;
}

.input:valid {
  color: #344f10;
}

.input:invalid {
  color: #eb5757;
}

.button {
  color: #f2f2f2;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
  background-color: #333;
  border: 1px solid #0000;
  max-width: 280px;
  height: 60px;
  padding: 19px 20px 20px;
  font-size: 16px;
  font-weight: 600;
  line-height: 21px;
  display: block;
}

@media (any-hover: hover) {
  .button {
    cursor: pointer;
    transition: border .3s, background-color .3s, color .3s;
  }

  .button:hover {
    color: #333;
    background-color: #e4dfd3;
    border: 1px solid #333;
  }
}

.link-hover {
  width: max-content;
  position: relative;
}

.link-hover:after {
  content: "";
  background-color: #344f10;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: -10px;
  left: 0;
}

@media (any-hover: hover) {
  .link-hover:after {
    content: "";
    transform-origin: 0;
    transition: transform .3s;
    transform: scale(0);
  }

  .link-hover:hover:after {
    transform: scale(1);
  }
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
}

@media (any-hover: hover) {
  .icon {
    transition: transform .3s;
    transform: scale(1);
  }

  .icon:hover {
    transform: scale(1.1);
  }
}

.icon--open {
  background: url("menu.2fa0a1fb.svg");
}

.icon--close {
  background: url("close.20e64e70.svg");
}

.text {
  color: #333;
  font-size: 14px;
  line-height: 150%;
}

@media (width >= 479.98px) {
