* {
  padding: 0;
  margin: 0;
  border: 0;
}
  
*,
*::before,
*::after {
  box-sizing: border-box;
}
  
html {
  font-family: $font-family;
  font-size: $font-size;
  scroll-behavior: smooth;
}
  
html,
body {
  height: 100%;
  min-width: $minWidth;
}
  
body {
  color: $main-color;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
  
input,
button,
textarea {
  font-family: $font-family;
  font-size: inherit;
}
  
button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
}
  
a {
  color: inherit;
}
  
a:link,
a:visited {
  text-decoration: none;
}
  
a:hover {
  text-decoration: none;
}
  
ul,
ol {
  list-style: none;
}
  
img {
  vertical-align: top;
}
  
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}